:host {
    padding: 15px;
    display: flex;
    flex-direction: column;
    .sub-text {
        margin-bottom: 5px;
    }
    .green-view,
    .yellow-view {
        padding: 0 5px;
        gap: 10px;
        height: 21px;
        width: fit-content;
        border-radius: 5px;
    }
    .green-view {
        color: var(--green);
        background: rgba(26, 183, 126, 0.05);
    }
    .yellow-view {
        background: rgba(248, 248, 248, 0.05);
    }
}
